import React from 'react'
import ReactDOM from 'react-dom'
import { BrowserRouter } from 'react-router-dom'
import App from './App'
import './index.css'

ReactDOM.render(
  
	<BrowserRouter basename="/My-Reads-React-Udacity">

	<App />
	</BrowserRouter>, 
	document.getElementById('root'))



